.customButton {
  height: 40px;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 40px;
  text-align: center;
  border-radius: 4px;
  cursor: default;
  transition: 100ms;
  &.default {
    background: #002C5E;
    color: #fff;
    &:hover{
      background: linear-gradient(0deg, rgba(255, 255, 255, 0.2), rgba(255, 255, 255, 0.2)), #002C5E;
    }
    &:active{
      background-color: #2D6CDF;
    }
    &.disable {
      cursor: not-allowed;
      background: #E3E6F5;
      color: #B9C1CF;
    }
  }
  &.normal {
    background: #FFFFFF;
    border: 1px solid #002C5E;
    box-sizing: border-box;
    border-radius: 4px;
    color:#002C5E;
    &:hover{
      border-color: #495C8C;
      color: #495C8C;
    }
    &:active{
      border-color: #495C8C;
      background-color: #E3E6F5;
    }
    &.disable {
      cursor: not-allowed;
      border-color: #B9C1CF;
      background: #E5E5E5;
      color: #B9C1CF;
    }
  }
  &.link {
    background: transparent;
    color: #002C5E;
    &:hover{
      color: #002C5E;
    }
    &:active{
      color: #2D6CDF;
    }
    &.disable {
      cursor: not-allowed;
      color: #B9C1CF;
    }
  }
  &.yellow {
    background: #F0D43A;
    color: #002C5E;
    &:hover{
      background: linear-gradient(0deg, rgba(255, 255, 255, 0.2), rgba(255, 255, 255, 0.2)), #F0D43A;
      color: #002C5E;
    }
    &:active{
      background: linear-gradient(0deg, rgba(0, 0, 0, 0.1), rgba(0, 0, 0, 0.1)), #F0D43A;
      color: #3A567A;
    }
    &.disable {
      cursor: not-allowed;
      background: #FFF5BD;
      color: #B9C1CF;
    }
  }
}
