.HotelDetail {
  .boxWrap {
    padding: 32px 40px;
    position: relative;
    width: 100%;
    background: #FFFFFF;
    box-shadow: 0px 0px 3px rgba(0, 0, 0, 0.15);
    border-radius: 4px;
  }

  .tripDetail {
    margin-top: 25px;
    display: flex;
    justify-content: space-between;
    .tripDetailLeft {
      flex: 1;
      margin-right: 20px;
      .boxWrap {
        margin-bottom: 16px;
      }
    }
    .tripDetailRight {
      width: 326px;
      .tripDetailRightBtn {
        .customButton {
          margin-bottom: 12px;
        }
      }
    }
    .orderTitle {
      font-size: 18px;
      line-height: 140%;
      color: #000000;
      margin-top: 16px;
      margin-bottom: 6px;
    }
  }
}
