.order-list-table {
  .btn {
    height: 48px;
    padding: 0 32px;
    border-radius: 6px;
    margin-left: 8px;
    font-weight: normal;

    &.warning {
      color: #002C5E;
      background-color: #F0D43A;
      border: 1px solid #F0D43A;
    }

    &.warning:hover{
      background-color: #F0D43A80;
    }

    &.warning:focus{
      background: linear-gradient(0deg, rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.2)), #F0D43A;
      box-shadow:0 0 0 1px rgba(0, 0, 0, 0);
      border: 1px solid transparent;
    }
  }
  .split {
    width: 10px;
  }
  .chakra-checkbox {
    .chakra-checkbox__control {
      width: 24px;
      height: 24px;
      border: 2px solid #8B8B8B;
      border-radius: 4px;
    }
  }

  .order-list-table-top {
    display: flex;
    align-items: end;
    justify-content: space-between;
    margin-bottom: 13px;

    .total {
      font-weight: 600;
      font-size: 20px;
      line-height: 28px;
      color: #4A4A4A;
      display: flex;
      align-items: center;
      span {
        font-weight: normal;
        font-size: 18px;
        padding-left: 10px;
        color: #002C5E;
      }
    }
    .btn-group {
      display: flex;
      align-items: center;
    }
  }

  .order-list-table-content {
    .order-list-table-checkbox{
      &.checked {
        .chakra-checkbox__control {
          background-color: #002C5E;
          border-color: #002C5E;
          color: #fff;
        }
      }
      .chakra-checkbox__control {
        border: 1px solid #4A4A4A;
        border-radius: 1px;
        color: #fff;
        background-color: #002C5E;
      }
      .chakra-checkbox__control[data-indeterminate]{
        background-color: #002C5E;
      }
    }

    .order-list-table-header {
      display: flex;
      align-items: center;
      background: rgba(255, 255, 255, 0.5);
      height: 75px;
      color: #859EB9;
      font-size: 14px;
      line-height: 120%;

      .header-cell {
        display: flex;
        align-items: center;
        // justify-content: center;
        height: 100%;
        cursor: default;
        &.right {
          justify-content: flex-end;
        }
      }


      .chakra-checkbox {
        .chakra-checkbox__control {
          width: 24px;
          height: 24px;
          border: 2px solid #FAFAFA;
          border-radius: 4px;
        }
        .chakra-checkbox__control[data-focus], .chakra-checkbox__control:focus {
          box-shadow: 0 0 0 0px rgba(0,0,0,0);
        }
      }
    }

    .order-list-table-body {
      border: 1px solid #859EB9;
      margin-bottom: 48px;
      background-color: #ffffff;
      .row:hover{
        background: #EBEEF5;
      }
      .row {
        display: flex;
        align-items: center;
        height: 84px;
        font-size: 14px;
        color: #002C5E;
        border-bottom: 1px solid #859EB9;
        transition: 300ms;
        &:last-child{
          border-bottom: none
        }
        .cell {
          display: flex;
          align-items: center;
          justify-content: center;
          height: 100%;
          .order-list-table-checkbox{
            &.checked {
              .chakra-checkbox__control {
                background-color: #002C5E;
                border-color: #002C5E;
                color: #fff;
              }
            }

            .chakra-checkbox__control {
              border: 2px solid #8B8B8B;
              border-radius: 4px;
              color: #fff;
              background-color: #fff;
            }

            .chakra-checkbox__control[data-indeterminate]{
              background-color: #002C5E;
            }

            .chakra-checkbox__control[data-checked]{
              background-color: #002C5E;
              border-color: #002C5E;
            }

            .chakra-checkbox__control[data-focus], .chakra-checkbox__control:focus {
              box-shadow: 0 0 0 0px rgba(0,0,0,0);
            }

          }
          .editIcon {
            width: 40px;
            height: 40px;
            background-image: url(../../assets/icons/edit.png);
            background-size: 16px 16px;
            background-repeat: no-repeat;
            background-position: center center;
            cursor: pointer;
            &:hover {
              background-image: url(../../assets/icons/editActive.png);
            }
          }
          &.sep {
            border-right: 1px solid #D7D7D7;
          }

          &.order-no {
            cursor: pointer;
            color: #002C5E;
            justify-content: start;
          }

          &.order-name{
            cursor: pointer;
            .product-name, .product-info {
              box-sizing: border-box;
              // padding: 0 50px;
              color: #002C5E;
            }
            .product-name {
              font-weight: 600;
              color: #002C5E;
              overflow : hidden;
              line-height: 120%;
              max-height: 34px;
              text-overflow: ellipsis;
              display: -webkit-box;
              -webkit-line-clamp: 1;
              -webkit-box-orient: vertical;
            }
          }

          .product-info {
            color: #8B8B8B;
            margin-top: 4px;
            overflow : hidden;
            line-height: 120%;
            max-height: 34px;
            text-overflow: ellipsis;
            display: -webkit-box;
            -webkit-line-clamp: 2;
            -webkit-box-orient: vertical;
          }

          .status {
            height: 20px;
            color: #53C0A6;
            background-color: #EDFFF9;
            border-radius: 34px;
            font-size: 14px;
            line-height: 20px;
            padding: 0 13px;
          }
        }
      }
    }

    .order-list-table-pagination {
      width: 100%;
      text-align: right;
    }
  }
}
