.OrderDetailTitle {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid #EBEEF5;
  padding-bottom: 8px;
  .title {
    font-style: normal;
    font-weight: 500;
    font-size: 24px;
    line-height: 130%;
    color: #333333;
  }
  .money {
    font-size: 20px;
    line-height: 140%;
    color: #002C5E;
    span {
      font-size: 14px;
    }
    i {
      font-style: normal;
      font-weight: 500;
    }
  }
  .button {
    font-size: 12px;
    line-height: 20px;
    color: #2D6CDF;
    cursor: pointer;
    &:hover {
      color: #859EB9;
    }
    &:active {
      color: #2D6CDF;
    }
  }
}
