.orderForm {
  margin: 10px 0;
  .searchAndSelect {
    display: flex;
  }
  .space {
    width: 16px;
  }
  .timeWrap {
    margin-top: 16px;
    display: flex;
    align-items: center;
  }
  .timeType {
    margin-right: 38px;
    .chakra-radio {
      margin-right: 40px;
    }
    .chakra-radio__control {
      background: #FFFFFF;
      border-color: #859EB9;
      color: #002C5E;
      width: 20px;
      height: 20px;
      border-width: 1px;
      display: flex;
      align-items: center;
      justify-content: center;
      &[aria-checked=true]::before,
      &[data-checked]::before {
        width: 12px;
        height: 12px;
      }
    }
  }
  .selectTime {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-bottom: 16px;
    border-bottom: 1px solid #859EB9;
  }
}
