.order {
  margin-top: 8px;
  // height: 338px;
  .orderPay {
    padding: 16px 0;
    border-bottom: 1px solid #EBEEF5;
  }
  .orderSupplier {
    padding: 16px 0;
  }
}
.editAmount {
  width: 221px;
  margin: 16px auto 0 auto;
  .errorInput {
    border-color: #FE5D6D;
    box-shadow: none;
  }
  .errorMessage {
    font-size: 16px;
    line-height: 140%;
    color: #FE5D6D;
    display: flex;
    align-items: center;
    margin-top: 4px;
    i {
      width: 20px;
      height: 20px;
      background-image: url(../../../assets/icons/error.png);
      background-size: contain;
      background-repeat: no-repeat;
      margin-right: 5px;
    }
  }
}
