.OrderDetailItem {
  .title {
    font-size: 14px;
    line-height: 140%;
    color: #8B8B8B;
    margin-bottom: 4px;
  }
  .value {
    font-size: 14px;
    line-height: 140%;
    color: #002C5E;
    span {
      padding-left: 8px;
      color: #2D6CDF;
      font-size: 14px;
      cursor: pointer;
      &:hover {
        color: #859EB9;
      }
      &:active {
        color: #2D6CDF;
      }
    }
  }
  .status {
    border-radius: 16px;
    width: 100px;
    height: 32px;
    display: flex;
    justify-content: center;
    align-items: center;
    .dot {
      width: 10px;
      height: 10px;
      border-radius: 6px;
      margin-right: 4px;
    }
  }
  .cancelled, .expired {
    background: #E9E9E9;
    color: #939393;;
    .dot {
      background: #939393;
    }
  }
  .cancelling, .waitPay, .toConfirmed {
    background: #FFE9EA;;
    color: #FF6770;
    .dot {
      background: #FF6770;
    }
  }
  .confirmed, .traveled {
    background: #DFF5F1;
    color: #41C7AF;
    .dot {
      background: #41C7AF;
    }
  }
}
