.workLog {
  padding-top: 20px;
  .logItem {
    width: 242px;
    height: 133px;
    display: flex;
    align-items: center;
    overflow: hidden;
    &:first-child {
      .dot {
        &::before {
          background: transparent;
        }
      }
    }
    &:last-child {
      .dot {
        &::after {
          background: transparent;
        }
      }
    }
  }

  .dot {
    width: 16px;
    height: 16px;
    background: #002C5E;
    border-radius: 16px;
    position: relative;
    &::after {
      content: '';
      position: absolute;
      width: 1px;
      height: 89px;
      background: #002C5E;
      left: 7px;
      top: 8px;
    }
    &::before {
      content: '';
      position: absolute;
      width: 1px;
      height: 89px;
      background: #002C5E;
      left: 7px;
      bottom: 8px;
    }
  }
  .description {
    width: 212px;
    position: relative;
    margin-left: 14px;
    &::after {
      content: '';
      display: block;
      width: 0;
      height: 0;
      border: 8px solid transparent;
      border-right: 12px solid #EBEEF5;
      position: absolute;
      left: -17px;
      top: calc(50% - 8px);
    }
    .content {
      background: #EBEEF5;
      border-radius: 4px;
      padding: 12px;
    }
  }
  .time {
    font-size: 14px;
    line-height: 140%;
    color: #859EB9;
  }
  .text {
    font-size: 14px;
    line-height: 140%;
    color: #002C5E;
  }
}
