.search {
  border-color: #859EB9;
  background-color: #ffffff;
  .icon {
    width: 20px;
    height: 20px;
    display: inline-block;
    background-image: url('../../assets/icons/search-default.png');
    background-size: contain;
  }
  .chakra-input:focus{
    border-color: #002C5E;
    box-shadow: 0 0 0 1px #002C5E;
    & + div {
      .icon {
        background-image: url('../../assets/icons/seach-active.png');
      }
    }
    &:not([value=""]):not(:focus):invalid{
      background: tomato;
    }
  }
}
